@import url('https://fonts.googleapis.com/css2?family=Space+Mono&display=swap');
@font-face {
    font-family: 'Calibre-Regular';
    font-style: normal;
    font-weight: normal;
    src: local('Calibre-Regular'), url('Calibre-Regular.woff') format('woff');
    }

*{
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
}

.container1{
    
    background-color: transparent;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: flex-start ;
    justify-content: center;
    animation: loader 1s ease forwards ;
}
@keyframes loader{
    0%{
        transform: translateY(30px);
        opacity: 0;
    }
    100%{
        transform: translateY(0px);
        opacity: 1;
    }
}
    


.greetings{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width:80%;

}




.heading1{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
}

 .one{
    font-size: 1.5vw;
    line-height: 5vw;
    word-spacing: 5px;
    font-weight: 400;
    color: #64ffda;
    font-family: 'SF Mono Regular'
} 

 .heading2{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;

}

.two{
    font-size: 7vw;
    line-height: 8vw;
    color: #ccd6f6;
    font-weight: 600;
     font-family: 'Calibre-Regular'
}

.heading3{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}


.three{
    font-size: 7vw;
    line-height: 8vw;
    color: #8892b0;
    word-spacing: 1px;
    font-weight: 600;
     font-family: 'Calibre-Regular'

}
.heading4{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}





.four{
    margin-top: 20px;
    font-size: 20px;
    line-height: 26px;
    font-weight: 400;
    color: #8892b0;
    font-family: 'Calibre-Regular'
} 


@media only screen and (max-width: 768px){

    .greetings{
        display: flex;
        flex-direction: column;
        justify-content: center;
        width:85%;

    
    }

    .heading1{
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: flex-start;
    }
    
     .one{
        font-size: 16px;
        line-height: 60px;
        word-spacing: 5px;
        font-weight: 400;
        color: #64ffda;
        font-family: 'SF Mono Regular'
    }

    .heading2{
        display: flex;
        flex-direction: column;
        justify-content: flex-start;   
    }
    
    .two{
        font-size: 8vw;
        line-height: 10vw;
        color: #ccd6f6;
        font-weight: 600;
         font-family: 'Calibre-Regular'
    }

    .heading3{
        display: flex;
        flex-direction: column;
    }
    
    
    .three{
        font-size: 8vw;
        line-height: 10vw;
        color: #8892b0;
        word-spacing: 1px;
        font-weight: 600;
         font-family: 'Calibre-Regular'
    
    }

    .heading4{
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
    }
    
    
    
    
    
    .four{
        margin-top: 20px;
        font-size: 18px;
        line-height: 23px;
        font-weight: 400;
        color: #8892b0;
        font-family: 'Calibre-Regular'
    }


    
}

